import { Metadata } from 'next';
import { notFound } from 'next/navigation';
import { CONTENT } from '../../../data/content';
import { getAllPosts, getPostBySlug } from '../../../lib/posts';
import BlogPostClient from './BlogPostClient';

type PageProps = {
  params: Promise<{
    slug: string;
  }>;
};

export async function generateMetadata({ params }: PageProps): Promise<Metadata> {
  console.log(params)
  const { slug } = await params;
  const postEn = getPostBySlug(slug, 'en');
  const postZh = getPostBySlug(slug, 'zh');
  const fallbackEn = CONTENT.en.blog.find((post) => post.slug === slug);
  const fallbackZh = CONTENT.zh.blog.find((post) => post.slug === slug);
  const post = postEn ?? postZh ?? fallbackEn ?? fallbackZh;

  if (!post) {
    return {
      title: 'Blog Post Not Found'
    };
  }

  return {
    title: `${post.title} | Neo-Brutalism Blog`,
    description: post.excerpt,
    openGraph: {
      title: post.title,
      description: post.excerpt,
      url: `https://neo-brutalism.example.com/blog/${slug}`,
      type: 'article'
    },
    twitter: {
      card: 'summary_large_image',
      title: post.title,
      description: post.excerpt
    }
  };
}

export default async function BlogPostPage({ params }: PageProps) {
  const { slug } = await params;

  const postsEn = getAllPosts('en');
  const postsZh = getAllPosts('zh');
  const postEn = postsEn.find((post) => post.slug === slug) ?? null;
  const postZh = postsZh.find((post) => post.slug === slug) ?? null;
  const fallbackEn = CONTENT.en.blog.find((post) => post.slug === slug) ?? null;
  const fallbackZh = CONTENT.zh.blog.find((post) => post.slug === slug) ?? null;

  if (!postEn && !postZh && !fallbackEn && !fallbackZh) {
    notFound();
  }

  return <BlogPostClient posts={{ en: postEn ?? fallbackEn, zh: postZh ?? fallbackZh }} />;
}

export async function generateStaticParams() {
  const slugsEn = getAllPosts('en').map(post => post.slug);
  const slugsZh = getAllPosts('zh').map(post => post.slug);
  const fallbackSlugsEn = CONTENT.en.blog.map(post => post.slug).filter(Boolean);
  const fallbackSlugsZh = CONTENT.zh.blog.map(post => post.slug).filter(Boolean);
  const allSlugs = Array.from(new Set([...slugsEn, ...slugsZh, ...fallbackSlugsEn, ...fallbackSlugsZh]));

  return allSlugs.map(slug => ({ slug }));
}

